<template>
  <div>
    <v-container>
      <title-ribbon v-bind="{ title: `Únete a nuestro Equipo` }" />
      <v-row class="form-trabajo__row">
        <v-col class="col-12 col-md-3 form-trabajo-col__left">
          <div class="ftl_img">
            <v-img
              class="img"
              max-height="86"
              max-width="400"
              :src="
                `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/general/logo_web_bpdgpj`
              "
            />
          </div>
          <div class="ftl_title">
            <h2>Beneficios Centro de Terapia</h2>
          </div>
          <hr>
          <div class="ftl_text">
            <ul>
              <li>
                Integrar un equipo multidisciplinario especializado en salud
                mental con más de 40 años de trayectoria.
              </li>
              <li>
                Sistema moderno e integrado de ficha electrónica que te
                permitirá hacer tus informes, certificados o recetas desde la
                comodidad de tu hogar.
              </li>
              <li>
                Contamos con un proactivo personal dispuesto a ayudarte en todos
                tus requerimientos. Estamos confirmando constantemente las horas
                de tus pacientes agendados.
              </li>
              <li>Apoyo en temas técnicos complejos.</li>
              <li>
                Puedes participar en todas aquellas actividades extras de
                nuestro centro como: Reuniones Clínicas de capacitación,
                actualización de temas ligados a la salud mental y discusión de
                casos complejos.
              </li>
              <li>
                Céntrica ubicación de nuestras sucursales con fácil acceso a
                estacionamientos y transporte público.
              </li>
              <li>
                Posibilidad de usar nuestra completa plataforma online de
                atención de pacientes.
              </li>
            </ul>
          </div>
        </v-col>
        <v-col class="col-12 col-md-9 form-trabajo-col__right">
          <div class="ftr__box_upper">
            Si estás interesado/a en formar parte de nuestro equipo de
            terapeutas, completa el siguiente formulario:
          </div>
          <div class="ftr__box_up">
            <validation-observer
              ref="observer"
              v-slot="{ invalid }"
            >
              <slide-x-left-transition>
                <form
                  class="px-0 mx-0 my-0"
                  @submit.prevent="validateForm()"
                >
                  <v-card style="margin: 0px;">
                    <v-row class="d-flex justify-center">
                      <v-col class="col-12 col-lg-6">
                        <v-row>
                          <v-col>
                            <validation-provider
                              v-slot="{ errors }"
                              rules="required|max:90"
                              name="Nombres"
                            >
                              <label class="grey--text">
                                Nombres
                              </label>
                              <v-text-field
                                v-model="nombres"
                                :error-messages="errors"
                                color="success"
                                filled
                                dense
                              />
                            </validation-provider>
                            <validation-provider
                              v-slot="{ errors }"
                              rules="rut|required"
                              name="Rut"
                            >
                              <label class="grey--text">
                                Rut
                              </label>
                              <v-text-field
                                v-model="rut"
                                :error-messages="errors"
                                color="success"
                                filled
                                dense
                                :validate-on-blur="false"
                                name="user[rut]"
                                flat
                              />
                            </validation-provider>
                            <validation-provider
                              v-slot="{ errors }"
                              rules="required|numeric|max:50"
                              name="Celular o Teléfono"
                            >
                              <label class="grey--text">
                                Teléfono
                              </label>
                              <v-text-field
                                v-model="telefono"
                                :error-messages="errors"
                                color="success"
                                type="number"
                                filled
                                dense
                              />
                            </validation-provider>
                            <validation-provider
                              v-slot="{ errors }"
                              name="Años de Experiencia Clínica"
                              rules="required|max:2|numeric"
                            >
                              <label class="grey--text">
                                Años de Experiencia Clínica
                              </label>
                              <v-text-field
                                v-model="anosExperiencia"
                                :error-messages="errors"
                                color="success"
                                filled
                                dense
                              />
                            </validation-provider>
                            <validation-provider
                              v-slot="{ errors }"
                              name="Grupo Etario de trabajo"
                              rules="required"
                            >
                              <label class="grey--text">
                                Grupo Etario de trabajo
                              </label>
                              <v-combobox
                                v-model="grupoEtario"
                                :items="optionsGrupoEtario"
                                :error-messages="errors"
                                hide-selected
                                label="Seleccione..."
                                multiple
                                persistent-hint
                                small-chips
                                :menu-props="{
                                  closeOnContentClick: true,
                                }"
                                filled
                                color="success"
                              />
                            </validation-provider>
                            <validation-provider
                              v-slot="{ errors }"
                              rules="required|max:50"
                              name="Expectativa Honorarios"
                            >
                              <label class="grey--text">
                                Expectativa Honorarios
                              </label>
                              <v-text-field
                                v-model="expeHonorarios"
                                :error-messages="errors"
                                color="success"
                                type="text"
                                filled
                                dense
                              />
                            </validation-provider>
                            <validation-provider
                              v-slot="{ errors }"
                              name="Disponibilidad para Trabajar"
                              rules="required"
                            >
                              <label class="grey--text">
                                Sucursal en la cual te gustaría trabajar
                              </label>
                              <v-combobox
                                v-model="dispSucursalUnformatted"
                                :items="sucursales"
                                item-text="Nombre"
                                item-value="Nombre"
                                :error-messages="errors"
                                hide-selected
                                label="Seleccione..."
                                multiple
                                persistent-hint
                                small-chips
                                filled
                                :menu-props="{
                                  closeOnContentClick: true,
                                }"
                                color="success"
                              />
                            </validation-provider>

                            <validation-provider
                              v-slot="{ errors }"
                              name="Archivo"
                              rules="ext:doc,docx,pdf|size:1000"
                            >
                              <label class="grey--text">
                                Agrega tu CV (opcional)
                              </label>
                              <v-file-input
                                v-model="archivo"
                                small-chips
                                :error-messages="errors"
                                label="Formatos: doc, docx, pdf (1mb max)"
                                dense
                                filled
                                color="success"
                              />
                            </validation-provider>
                          </v-col>
                        </v-row>
                      </v-col>
                      <v-col class="col-12 col-lg-6">
                        <v-row>
                          <v-col>
                            <div>
                              <validation-provider
                                v-slot="{ errors }"
                                rules="required|max:90"
                                name="Apellidos"
                              >
                                <label class="grey--text">
                                  Apellidos
                                </label>
                                <v-text-field
                                  v-model="apellidos"
                                  :error-messages="errors"
                                  color="success"
                                  filled
                                  dense
                                />
                              </validation-provider>
                              <validation-provider
                                v-slot="{ errors }"
                                rules="required|email|max:100"
                                name="E-mail"
                              >
                                <label class="grey--text">
                                  E-mail
                                </label>
                                <v-text-field
                                  v-model="email"
                                  :error-messages="errors"
                                  color="success"
                                  type="text"
                                  filled
                                  dense
                                />
                              </validation-provider>
                              <validation-provider
                                v-slot="{ errors }"
                                rules="required|max:50"
                                name="Especialidad"
                              >
                                <label class="grey--text">
                                  Especialidad
                                </label>
                                <v-select
                                  v-model="especialidad"
                                  :error-messages="errors"
                                  color="success"
                                  :items="especialidadesMenu"
                                  item-text="Nombre"
                                  :menu-props="{ maxHeight: '400' }"
                                  label="Seleccione Especialidad"
                                  persistent-hint
                                  filled
                                  dense
                                />
                              </validation-provider>
                              <validation-provider
                                v-slot="{ errors }"
                                rules="required|max:100"
                                name="Disponibilidad Horaria"
                              >
                                <label class="grey--text">
                                  Disponibilidad Horaria
                                </label>
                                <v-text-field
                                  v-model="dispHoraria"
                                  :error-messages="errors"
                                  color="success"
                                  type="text"
                                  filled
                                  dense
                                />
                              </validation-provider>
                              <validation-provider
                                v-slot="{ errors }"
                                rules="max:700"
                                name="Comentarios"
                              >
                                <label class="grey--text">
                                  Comentarios
                                </label>
                                <v-textarea
                                  v-model="comentarios"
                                  :error-messages="errors"
                                  filled
                                  name="input-7-4"
                                  label="Comentarios"
                                  color="success"
                                />
                              </validation-provider>
                            </div>
                            <div>
                              <fade-transition>
                                <div style="height: 57px;">
                                  <v-alert
                                    v-show="showRecaptchaAlert"
                                    dense
                                    text
                                    type="warning"
                                    style="padding: 1px; margin: 0px;"
                                    class="box"
                                  >
                                    Marque la casilla de verificación
                                  </v-alert>
                                  <v-alert
                                    v-show="showEnviado"
                                    dense
                                    text
                                    type="success"
                                    style="padding: 1px; margin: 0px;"
                                    class="box"
                                  >
                                    Formulario Enviado
                                  </v-alert>
                                </div>
                              </fade-transition>
                              <!-- testing -->
                              <!-- <vue-recaptcha
                                sitekey="6LeIxAcTAAAAAJcZVRqyHh71UMIEGNQ_MXjiZKhI"
                                @verify="onVerify"
                                @expired="onExpired"
                              /> -->
                              <!-- production -->
                              <vue-recaptcha
                                sitekey="6Lf7HQQeAAAAAC7MOA15SB35MQ8UwUh8wYG5rg-h"
                                @verify="onVerify"
                                @expired="onExpired"
                              />
                              <!-- mobile -->
                              <!-- <vue-recaptcha
                          sitekey="6Le-Bu8bAAAAAOIi7Am_cyMl4OqY0rn8fg7UI9ry"
                          @verify="onVerify"
                          @expired="onExpired"
                        /> -->
                              <div class="form-trabajo__buttons py-5">
                                <v-btn
                                  color="success"
                                  default
                                  type="submit"
                                  :disabled="invalid"
                                  :loading="btnLoader"
                                >
                                  Enviar
                                </v-btn>
                              </div>
                            </div>
                          </v-col>
                        </v-row>
                      </v-col>
                    </v-row>
                  </v-card>
                </form>
              </slide-x-left-transition>
            </validation-observer>
          </div>
          <div class="ftr__box_bottom">
            <p>
              Si tienes interés en ser parte de nuestro equipo no dudes en
              enviarnos tus datos
            </p>
            <h2>
              ¡TE ESPERAMOS!
            </h2>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
  import Vue from 'vue'
  import VueRecaptcha from 'vue-recaptcha'
  import { SlideXLeftTransition, FadeTransition } from 'vue2-transitions'
  import { mapState } from 'vuex'
  // import { extend } from 'vee-validate'

  export default {
    name: 'Trabajo',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      VueRecaptcha,
      SlideXLeftTransition,
      FadeTransition,
    },
    data: () => ({
      nombres: '',
      apellidos: '',
      rut: '',
      email: '',
      telefono: '',
      especialidad: '',
      anosExperiencia: '',
      grupoEtario: '',
      dispHoraria: '',
      dispSucursalUnformatted: '',
      expeHonorarios: '',
      comentarios: '',
      recaptchaVerified: false,
      showRecaptchaAlert: false,
      showEnviado: false,
      btnLoader: false,
      optionsGrupoEtario: ['Niños', 'Adolescentes', 'Adultos', 'Adultos Mayores'],
      archivo: null,
    }),
    computed: {
      ...mapState('web', ['especialidadesMenu', 'sucursales']),
      computedOrdenarFecha () {
        return this.ordenarFecha(this.fechaNacimiento)
      },
    },
    watch: {
      recaptchaVerified: function () {
        if (this.recaptchaVerified) {
          this.hideMsg()
        }
      },
      grupoEtario (val) {
        if (val.length > 4) {
          this.$nextTick(() => this.grupoEtario.pop())
        }
      },
      rut: function () {
        this.rut = this.rut.replace(/^0+|[^0-9kK]+/g, '').toUpperCase()
        const len = this.rut.length - 1
        if (len > 3) {
          const dv = this.rut[len]
          const beforeDv = this.rut
            .substring(0, len)
            .replace(/\B(?=(\d{3})+(?!\d))/g, '.')
          this.rut = beforeDv + '-' + dv
        }
      },
    },
    methods: {
      async validateForm () {
        const isValid = await this.$refs.observer.validate()
        if (!isValid) {
          return false
        }

        if (!this.recaptchaVerified) {
          this.showRecaptchaAlert = true
          return false
        }

        console.log('validando')
        this.btnLoader = true

        const rutCompleto = this.rut
          .split('.')
          .join('')
          .trim()

        const [rut, digito] = rutCompleto.split('-')

        const dispSucursal = []
        this.dispSucursalUnformatted.map(suc => {
          if (suc.CodSucursal) {
            dispSucursal.push(suc.CodSucursal)
          }
        })

        const body = new FormData()
        body.append('nombres', this.nombres)
        body.append('apellidos', this.apellidos)
        body.append('rut', rut)
        body.append('digito', digito)
        body.append('email', this.email)
        body.append('telefono', this.telefono)
        body.append('especialidad', this.especialidad)
        body.append('anosExperiencia', this.anosExperiencia)
        body.append('grupoEtario', this.grupoEtario)
        body.append('dispHoraria', this.dispHoraria)
        body.append('dispSucursal', dispSucursal)
        body.append('expeHonorarios', this.expeHonorarios)
        body.append('comentarios', this.comentarios)
        body.append('archivo', this.archivo)

        // console.log(this.archivo)

        Vue.axios
          .post('/api/formularios/trabajo', body, {
            headers: {
              'Content-Type': 'multipart/form-data',
            },
          })
          .then(response => {
            // console.log(response)
            this.showEnviadoMsg()
            this.limpiarFormulario()
            this.btnLoader = false
          })
          .catch(error => {
            console.log(error.message)
          })
      },
      onVerify () {
        if (!this.recaptchaVerified) {
          this.recaptchaVerified = true
        }
      },
      onExpired () {
        this.recaptchaVerified = false
      },
      hideMsg () {
        this.showRecaptchaAlert = false
      },
      showEnviadoMsg () {
        this.showEnviado = true
        setTimeout(() => {
          this.showEnviado = false
        }, 10000)
      },
      printSucursales () {
        const sucursalesFinales = []
        this.dispSucursalUnformatted.map(suc => {
          sucursalesFinales.push(suc.CodSucursal)
        })
        console.log(sucursalesFinales)
      },
      limpiarFormulario () {
        this.nombres = ''
        this.apellidos = ''
        this.rut = ''
        this.email = ''
        this.telefono = ''
        this.especialidad = ''
        this.anosExperiencia = ''
        this.grupoEtario = ''
        this.dispHoraria = ''
        this.dispSucursalUnformatted = ''
        this.expeHonorarios = ''
        this.comentarios = ''
        this.archivo = null
        requestAnimationFrame(() => {
          // Reseteamos vee
          this.$refs.observer.reset()
        })
        this.onExpired() // Reseteamos captcha
      },
    },
    metaInfo: {
      title: 'Únete A Nuestro Equipo',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
